<template>
  <div
    :class="{
      'tw-h-screen': true,
      'tw-overflow-hidden': !isInitted,
    }"
  >
    <Preloader v-if="!isInitted" />
    <IntroCanvas />
    <Header />
    <div class="sections">
      <Guide />
      <div v-multi-ref:sections class="section tw-z-10">
        <Intro />
      </div>
      <div class="section tw-container">
        <div class="row">
          <div class="col-12 col-lg-7 project__imageContainer">
            <MyWorkCanvas />
          </div>

          <div class="col-12 col-lg-5">
            <div
              v-for="(project, index) in projects"
              :key="index"
              v-multi-ref:sections
            >
              <MyWork :project="project" class="tw-flex tw-items-center" />
            </div>
          </div>
        </div>
      </div>
      <div
        v-multi-ref:sections
        class="section section--flow section--centered contact"
      >
        <ContactMe />
      </div>
    </div>
  </div>
</template>

<script>
import Preloader from "@/components/common/Preloader";
import Guide from "@/components/common/Guide";
import Header from "@/components/common/Header";
import Intro from "@/components/home/Intro";
import MyWork from "@/components/home/MyWork";
import ContactMe from "@/components/home/ContactMe";
import IntroCanvas from "@/components/home/IntroCanvas.vue";
import MyWorkCanvas from "@/components/home/MyWorkCanvas.vue";
import { projects } from "@/configs/projects";
import { Component, Vue } from "vue-property-decorator";

const updateScrollState = function () {
  const sections = this.$refs.sections;

  let currentSection = 0;
  let sectionProgress = 0.0;
  const sectionCount = sections.length;
  const offset = 30;

  for (let index = sectionCount - 1; index >= 0; index--) {
    const section = sections[index];
    const elRect = section.getBoundingClientRect();
    const elTop = elRect.top;

    if (elTop - offset < 0) {
      currentSection = index;
      sectionProgress = Math.abs(elTop) / elRect.height;
      break;
    }
  }

  this.$store.commit("setScrollState", {
    currentSection,
    sectionProgress,
  });
};

@Component({
  components: {
    IntroCanvas,
    Header,
    Intro,
    Guide,
    MyWork,
    ContactMe,
    Preloader,
    MyWorkCanvas,
  },
})
class Home extends Vue {
  get updateScrollStateFunc() {
    return updateScrollState.bind(this);
  }
  get isInitted() {
    return this.$store.state.isInitted;
  }
  projects = projects;

  mounted() {
    window.addEventListener("scroll", this.updateScrollStateFunc);
  }
  beforeDestroy() {
    window.removeEventListener("scroll", this.updateScrollStateFunc);
  }
}
export default Home;
</script>
