import Vue from "vue";
import App from "./App.vue";
import { Plugin } from "vue-fragment";
import Vue2TouchEvents from "vue2-touch-events";
import "vue-multi-ref";

Vue.use(Plugin);
Vue.use(Vue2TouchEvents, {
  disableClick: true,
  swipeTolerance: 30,
});

Vue.config.productionTip = false;

import store from "./store";

new Vue({
  render: h => h(App),
  store,
}).$mount("#app");
