



























import { projects } from "@/configs/projects";
import Vue from "vue";
import Component from "vue-class-component";

const ABOUT_ME = 1;
const MY_WORK = 2;
const CONTACT = 3;

@Component
class Guide extends Vue {
  get isInitted() {
    return this.$store.state.isInitted;
  }
  get currentSection() {
    return this.$store.state.scrollState.currentSection;
  }
  get currentSectionId() {
    if (this.currentSection < 1) return ABOUT_ME;

    if (this.currentSection >= 1 && this.currentSection < projects.length + 1)
      return MY_WORK;

    return CONTACT;
  }
  get title(): string {
    if (this.currentSectionId === ABOUT_ME) {
      return "About me";
    }

    if (this.currentSectionId === MY_WORK) return "My Work";
    if (this.currentSectionId === CONTACT) return "Contact";

    return "";
  }
  get number() {
    return "0" + (this.currentSection + 1);
  }
}
export default Guide;
