
/* eslint-disable @typescript-eslint/camelcase */
import { projects } from "@/configs/projects";
import {
  AdditiveBlending,
  NearestFilter,
  Mesh,
  PerspectiveCamera,
  PlaneBufferGeometry,
  Scene,
  ShaderMaterial,
  Texture,
  TextureLoader,
  Vector2,
  WebGLRenderer,
} from "three";
import { Vue, Component } from "vue-property-decorator";
import myWorkVS from "@/misc/mywork/vs";
import myWorkFS from "@/misc/mywork/fs";
import gsap from "gsap";

class GlobalConfig {
  scrollProgress = 1.0;
  sectionProgress = 0.5;
  transitBlend = 0.0;
  currentSection = 0;
}

const data = new GlobalConfig();

if (process.env.NODE_ENV != "production") {
  // eslint-disable-next-line @typescript-eslint/no-var-requires
  const dat = require("dat.gui");
  const gui = new dat.GUI();
  gui.add(data, "transitBlend", 0.0, 1.0, 0.01);
  gui.add(data, "sectionProgress", 0.0, 10);
  gui.add(data, "currentSection", 0, 4);
}

interface IState {
  isActive: boolean;
  transitBlend?: number;
}

class MyCanvas {
  public container!: HTMLElement;
  public state: IState = {
    isActive: true,
    transitBlend: 0.0,
  };
  public time = 0;
  public projectImages: Texture[] = [new Texture()];
  public scene!: Scene;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public uniforms: Record<string, { type?: string; value: any }> = {};
  public renderer?: WebGLRenderer;

  public plane!: Mesh;
  public camera = new PerspectiveCamera();

  public nextWorkImage!: Texture;
  public currentWorkImage!: Texture;

  public transitTimeline = gsap.timeline();

  constructor({ container, state }: { container: HTMLElement; state: IState }) {
    this.state = { ...this.state, ...state };
    this.init(container);
  }
  loadImages() {
    const loader = new TextureLoader();

    const promises: Promise<unknown>[] = [];

    function processTexture(texture: Texture) {
      texture.minFilter = NearestFilter;
    }

    projects.forEach((project, index) => {
      promises.push(
        new Promise(resolve => {
          loader.load(project.image, texture => {
            processTexture(texture);
            this.projectImages[index + 1] = texture;
            resolve(1);
          });
        })
      );
    });

    return Promise.all(promises);
  }
  async init(container: HTMLElement) {
    this.container = container;
    this.time = 0;

    await this.loadImages();

    this.initScene();
    this.addObjectsToScene();

    this.onWindowResize();
    this.animate();
  }
  addObjectsToScene() {
    this.projectImages.push(new Texture()); // Add blank images at the end of projects list
    this.addMyWorkSceneObject();
  }
  addMyWorkSceneObject() {
    const { clientWidth, clientHeight } = this.container;

    // Global Uniforms
    this.uniforms = {
      u_resolution: {
        type: "vec2",
        value: new Vector2(clientWidth, clientHeight),
      },
      time: {
        value: this.time,
      },
      u_workImage: {
        type: "t",
        value: this.currentWorkImage,
      },
      u_nextWorkImage: {
        type: "t",
        value: this.nextWorkImage,
      },
      u_transitBlend: {
        value: data.transitBlend,
      },
    };

    const geometry = new PlaneBufferGeometry(0.5, 0.375, 16, 16);

    const material = new ShaderMaterial({
      vertexShader: myWorkVS,
      fragmentShader: myWorkFS,
      uniforms: this.uniforms,
      blending: AdditiveBlending,
    });

    const workPlane = new Mesh(geometry, material);
    workPlane.name = "MyWork";
    workPlane.rotation.set(-0.03, 0, -0.01);

    this.plane = workPlane;
    this.scene.add(workPlane);
  }
  onWindowResize() {
    const { clientWidth, clientHeight } = this.container;

    if (!this.renderer) return;

    const rendererWidth = Math.max(window.innerWidth * 0.5 * 0.95, clientWidth);
    const rendererHeight = Math.max(
      window.innerHeight * 0.3 * 0.95,
      clientHeight
    );

    this.renderer.setSize(rendererWidth, rendererHeight, true);
    this.camera.aspect = clientWidth / clientHeight;
    this.camera.updateProjectionMatrix();

    if (window.innerWidth < 768) {
      this.plane.position.y = 0.1;
      this.plane.position.z = -0.3;
      this.renderer.setPixelRatio(0.9);
    } else {
      this.plane.position.y = 0;
      this.plane.position.z = -0.1;
      this.renderer.setPixelRatio(window.devicePixelRatio);
    }
  }
  initScene() {
    const { clientWidth, clientHeight } = this.container;

    this.currentWorkImage = this.projectImages[0]; // Blank texture
    this.nextWorkImage = this.projectImages[1]; // First work

    this.scene = new Scene();

    this.camera = new PerspectiveCamera(
      45,
      clientWidth / clientHeight,
      0.1,
      1000
    );
    this.camera.position.set(0, 0, 0.5);

    this.renderer = new WebGLRenderer({
      precision: "lowp",
      antialias: true,
      powerPreference: "high-performance",
      alpha: true,
    });

    this.renderer.autoClear = false;
    this.renderer.setClearColor(0xff0000, 0);
    this.renderer.getContext().getExtension("OES_standard_derivatives");
    this.container.appendChild(this.renderer.domElement);
  }
  update() {
    if (!this.uniforms) {
      return;
    }

    this.time += 0.01;
    this.uniforms.time.value = this.time;
    this.uniforms.u_transitBlend.value = data.transitBlend;
    this.uniforms.u_workImage.value = this.currentWorkImage;
    this.uniforms.u_nextWorkImage.value = this.nextWorkImage;
  }
  animate() {
    window.requestAnimationFrame(this.animate.bind(this));

    if (!this.state.isActive) {
      return;
    }

    this.update();
    this.render();
  }
  render() {
    if (!this.renderer) return;

    this.renderer.clear();
    this.renderer.clearDepth();
    this.renderer.render(this.scene, this.camera);
  }
  unpause() {
    this.state.isActive = true;
  }
  pause() {
    this.state.isActive = false;
  }
  transit(targetIdx: number) {
    this.currentWorkImage = this.projectImages[targetIdx];
    this.nextWorkImage = this.projectImages[targetIdx + 1];
  }
}

@Component
class MyWorkCanvas extends Vue {
  canvas?: MyCanvas;
  currentWorkIndex = 0;

  get onScrollCanvasEvent() {
    return this.onScrollCanvas.bind(this);
  }

  onScrollCanvas() {
    if (!this.canvas) {
      return;
    }

    const { currentSection, sectionProgress } = this.$store.state.scrollState;
    const totalProgress = currentSection + sectionProgress;

    data.currentSection = currentSection;
    data.sectionProgress = sectionProgress;
    data.scrollProgress = currentSection + sectionProgress;

    const ABOVE_SECTION_COUNT = 1;
    const projectsLength = this.canvas.projectImages.length;

    if (
      totalProgress > 0.1 &&
      currentSection <= projectsLength - ABOVE_SECTION_COUNT
    ) {
      data.transitBlend = sectionProgress;
      this.canvas.state.isActive = true;

      const newWorkIndex = Math.min(
        Math.max(0, currentSection),
        projectsLength
      );

      if (this.currentWorkIndex == newWorkIndex) return;

      this.currentWorkIndex = newWorkIndex;
      this.canvas?.transit(newWorkIndex);
      return;
    }

    this.canvas.state.isActive = false;
  }

  get onWindowResizeFunc() {
    return this.canvas?.onWindowResize.bind(this.canvas);
  }

  mounted() {
    const { currentSection, sectionProgress } = this.$store.state.scrollState;

    data.currentSection = currentSection;
    data.sectionProgress = sectionProgress;
    data.scrollProgress = currentSection + sectionProgress;

    document.addEventListener("scroll", this.onScrollCanvasEvent);
    this.onScrollCanvasEvent();

    this.canvas = new MyCanvas({
      container: this.$refs.canvas as HTMLElement,
      state: {
        isActive: false,
      },
    });

    if (!this.onWindowResizeFunc) return;
    window.addEventListener("resize", this.onWindowResizeFunc);
  }

  beforeDestroy() {
    document.removeEventListener("scroll", this.onScrollCanvasEvent);

    if (!this.onWindowResizeFunc) return;
    window.removeEventListener("resize", this.onWindowResizeFunc);
  }
}

export default MyWorkCanvas;
