import Vuex from "vuex";
import Vue from "vue";

Vue.use(Vuex);

const DEFAULT_STATE = {
  isInitted: false,
  scrollState: {
    currentSection: 0,
    sectionProgress: 0.0,
  },
  projectState: {
    currentProject: 0,
    projectList: [],
  },
};

const store = new Vuex.Store({
  state: DEFAULT_STATE,
  mutations: {
    setScrollState(state, payload) {
      state.scrollState = payload;
    },
    setCurrentProject(state, payload) {
      state.projectState.currentProject = payload;
    },
    setProjectList(state, payload) {
      state.projectState.projectList = payload;
    },
    setInitState(state, bool) {
      state.isInitted = Boolean(bool);
    },
  },
});

export default store;
