export function lerp(a: number, b: number, n: number): number {
  return (1 - n) * a + n * b;
}

let throttleTimerId: number | undefined;

export function throttle(func: Function, delay: number): void {
  // If setTimeout is already scheduled, no need to do anything
  if (throttleTimerId) {
    return;
  }

  // Schedule a setTimeout after delay seconds
  throttleTimerId = setTimeout(function() {
    func();

    throttleTimerId = undefined;
  }, delay);
}
