























import gsap from "gsap";
import Vue from "vue";
import Component from "vue-class-component";

@Component
class Preloader extends Vue {
  progress = 0.0;

  get progressText() {
    return Number(this.progress * 100) | 0;
  }

  get progressStyle() {
    return {
      transform: `scale(${this.progress})`,
    };
  }

  mounted() {
    const dusts = this.$refs.dusts as HTMLElement[];
    const segmentLength = 100 / dusts.length;
    const getSegmentOffset = (index: number) => {
      const elementOrder = (index / 2) | 0;
      const segmentOffset = segmentLength * elementOrder;
      return segmentOffset + "%";
    };

    gsap.set(dusts, {
      css: {
        left: (index: number) => {
          const isOdd = index % 2 === 0;

          if (!isOdd) return "";
          return getSegmentOffset(index);
        },
        right: (index: number) => {
          const isEven = index % 2 !== 0;

          if (!isEven) return "";

          return getSegmentOffset(index);
        },
      },
    });

    const tl = gsap.timeline({
      onComplete: () => {
        (this.$refs.loadingBar as HTMLElement).style.background = "transparent";

        const dustTl = gsap.timeline({
          onComplete: () => {
            this.$store.commit("setInitState", true);
          },
        });

        dustTl.addLabel("start");

        dustTl.set(dusts, {
          opacity: 1,
        });

        dustTl.to(
          dusts,
          {
            x() {
              return 20 * Math.random() - 15;
            },
            y() {
              return 30 + Math.random() * 25;
            },
            opacity: 0,
            duration: 2.5,
            stagger: 0.0015,
          },
          "start"
        );

        dustTl.to(
          this,
          {
            progress: 0,
            duration: 0.1,
          },
          "start"
        );

        dustTl.to(
          this.$refs.loadingGroup,
          {
            opacity: 0,
            duration: 0.3,
          },
          "-=1.3"
        );
      },
    });

    tl.fromTo(
      this,
      {
        progress: 0.0,
      },
      {
        progress: 0.3,
        duration: 5,
      }
    );

    tl.fromTo(
      this,
      {
        progress: 0.3,
      },
      {
        duration: 5,
        progress: 0.7,
      }
    );

    tl.fromTo(
      this,
      {
        progress: 0.7,
      },
      {
        duration: 3,
        progress: 1.0,
      }
    );

    window.addEventListener("load", () => {
      tl.timeScale(8);
    });
  }
}
export default Preloader;
